// SECTION main :: START

.s-main {
  padding-top: 200px;
  padding-bottom: 100px;
  @include adaptive(767) {
    padding-top: 150px;
  }

  &__title,
  &__subtitle,
  &__desc {
    padding-bottom: 40px;
    text-align: center;
  }

  &__title,
  &__subtitle {
    color: #000000;
  }

  &__title {
    font-size: 60px;
    font-weight: 900;
    line-height: 1.17;
    @include adaptive(1279) {
      font-size: 55px;
    }
    @include adaptive(1023) {
      font-size: 48px;
    }
    @include adaptive(767) {
      font-size: 42px;

      br {
        display: none;
      }
    }
  }

  &__subtitle {
    font-size: 40px;
    font-weight: 300;
    line-height: 1.25;
    @include adaptive(1279) {
      font-size: 36px;
    }
    @include adaptive(1023) {
      font-size: 33px;
    }
    @include adaptive(767) {
      font-size: 30px;
    }
  }

  &__desc {
    width: 100%;
    max-width: 758px;
    margin-left: auto;
    margin-right: auto;
    font-size: 19px;
    line-height: 1.58;
    color: #939393;
    @include adaptive(1279) {
      font-size: 18px;
    }
    @include adaptive(767) {
      font-size: 16px;
    }
  }

  &__btn {
    max-width: 239px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 10px 15px 0 rgba(205, 8, 75, 0.2);

    &:hover {
      color: #cd084b;
    }
  }
}

// SECTION main :: END